.game {
  height: 100vh;
  width: 100vw;
  color: white;
  text-align: center;
  position: relative;

  background-color: #1F3A93;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.boxes {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3.5px;

  position: absolute;
  width: 501px;
  height: 501px;
}

.box {
  width: 160px;
  height: 160px;
  cursor: pointer;
}

svg {
  stroke: rgb(0, 0, 0);
  width: 501px;
  height: 501px;
  stroke-width: 10;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
}

line {
  stroke: white;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1.25s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

circle {
  stroke: #F62459;
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

path {
  stroke: #36DBD7;
  stroke-width: 12.5;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
          animation: dash 1s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 525px) {
  svg {
    width: 300px;
    height: 300px;
  }
}


